import React, { Fragment, useRef, useState } from 'react';
import "../../../../Css/Registration.css";
import { IoReturnUpBack } from "react-icons/io5";
import Axios from "axios";
import fdalogo from "../../assets/images/LOGO/fdalogo.png";


function Registrationpage() {

    const [username, setusername] = useState(null)
    const [email, setemail] = useState(null)
    const [state, setstate] = useState(null)
    const [mobile, setmobile] = useState(null)
    const [password, setpassword] = useState(null)
    const [confirmpassword, setconfirmpassword] = useState(null)
    const [correct, setcorret] = useState(false);
    const [uname, setuname] = useState("")
    const [dup, setdup] = useState("");
    const [check, setcheck] = useState(false)
    const [isChecked, setIsChecked] = useState(false);
    const formRef = useRef(null);

    const createuser = (event) => {
        event.preventDefault();
        if (password === confirmpassword) {
            if (isChecked) {
                Axios.post("https://server-3a0j.onrender.com/register", {
                    // Axios.post("http://localhost:9090/register", {
                    Username: username,
                    Email: email,
                    State: state,
                    Mobile: mobile,
                    Password: password,
                }).then(response => {
                    const work = response.data;
                    console.log(work);
                    setcheck(work.check);
                    console.log(work.errno)

                    if (work.check) {
                        formRef.current.reset();
                        setuname(username)
                    }
                    if (response.data.errno === 1062) {
                        setdup("User Exist! change username or email !")
                    } else {
                        setdup("")
                    }
                }).catch(err => {
                    console.log(err)
                })
            } else {
                alert("Please agree to the terms and conditions")
            }
        }
        else {
            setcorret(!correct);
        }
    }

    return (
        <Fragment>
            <div className="container-fluid" style={{ height: '115vh', fontFamily: "poppins", backgroundColor: "#023047" }}>
                <div className="container" >
                    <div className="row pt-4">
                        <div className="col-md-6 d-none d-md-block" style={{ height: '100vh', backgroundColor: "#023047", paddingTop: "30vh", fontFamily: "sans-serif", borderRight: "1px solid gray" }}>
                            <p className='fs-1 fw-bold text-center text-white'>Create New Account</p>
                            <p className='fs-6 text-center text-white'> You have to provide all of your valid information as we want.</p>
                            <p className='text-center'><button className='btn btn-primary backbtn' ><a className='text-decoration-none text-white' href="/"><IoReturnUpBack /> Back to home</a></button></p>
                        </div>
                        <div className="col-md-6 text-start" style={{ backgroundColor: "#023047", borderLeft: "1px solid gray" }}>
                            <div className="top text-center mb-5">
                                <a href="/" className="account-logo">
                                    <img src={fdalogo} height={"100px"} alt="logo" />
                                </a>
                            </div>

                            {/* Success Alert*/}

                            <div className={`${check ? "d-block" : "d-none"}`}>
                                <div className="alert alert-success alert-dismissible fade show" role="alert">
                                    <strong>{uname}</strong> register succussfully!
                                    <button type="button" className="btn-close" onClick={() => {
                                        setcheck(false)
                                    }}></button>
                                </div>
                            </div>
                            {/* here i am using formRef for reset the form data */}
                            <form className="row g-md-4 text-light" ref={formRef} method='post' onSubmit={createuser}>
                                <div className="col-md-6 ">
                                    <label htmlFor="inputusername" className="form-label">Username <span className='text-danger'>*</span></label>
                                    <input type="text" className='form-control' id="inputusername" pattern="[a-z0-9]+" title="Username must contain only lowercase letters" required onChange={(event) => {
                                        setusername(event.target.value)
                                        setdup("")
                                    }} />
                                    <span className='text-danger fst-italic' style={{ fontSize: "15px" }}> {dup}</span>
                                </div>
                                <div className="col-md-6">
                                    <label htmlFor="inputemail" className="form-label">E-Mail Address <span className='text-danger'>*</span></label>
                                    <input type="email" className="form-control" id="inputemail" onChange={(event) => {
                                        setemail(event.target.value)
                                    }} />
                                </div>
                                <div className="col-md-6">
                                    <label htmlFor="inputState" className="form-label">State <span className='text-danger'>*</span></label>
                                    <select id="inputState" className="form-select text-secondary" onChange={(event) => {
                                        setstate(event.target.value)
                                    }}>
                                        <option hidden>State</option>
                                        <option value="bihar">Bihar</option>
                                        <option value="jharkhand">Jharkhand</option>
                                    </select>
                                </div>
                                <div className="col-md-6 ">
                                    <label htmlFor="inputmobile" className="form-label">Mobile <span className='text-danger'>*</span></label>
                                    <input type="text" className="form-control" id="inputmobile" name="mobile" pattern="\+91[0-9]{10}" title="Mobile number must start with +91" required onChange={(event) => {
                                        setmobile(event.target.value)
                                    }} />
                                </div>
                                <div className="col-md-6 ">
                                    <label htmlFor="inputpassword" className="form-label">Password <span className='text-danger'>*</span></label>
                                    <input type="password" className="form-control" id="inputpassword" minLength="6" onChange={(event) => {
                                        setpassword(event.target.value)
                                    }} />
                                </div>

                                <div className="col-md-6">
                                    <label htmlFor="inputconfirmpassword" className="form-label">Confirm Password <span className='text-danger'>*</span></label>
                                    <input type="password" className="form-control" id="inputconfirmpassword" onChange={(event) => {
                                        setconfirmpassword(event.target.value)
                                        setcorret(false)
                                    }} />
                                    <span className={`fst-italic text-danger ${correct ? "d-block" : "d-none"}`}>Password not matched!</span>
                                </div>
                                <div className="col-md-12 regdiv2 ms-2 pt-1">
                                    <input
                                        type="checkbox"
                                        checked={isChecked}
                                        onChange={() => setIsChecked(!isChecked)}
                                    /> I agree to the <a href="/">company policy</a>,<a href="/">privacy policy</a>,<a href="/">terms and conditions</a><br />
                                </div>
                                <div className="col-md-12">
                                    <button type='submit' className="mt-2 btn btn-primary w-100 fs-4">Register</button>
                                    <button type='reset' className="mt-2 btn btn-primary w-100 fs-4">Reset</button>
                                </div>
                                <div className='col-md-12 regdiv2'>
                                    Already have an account? <a href="Login-page">Login Now</a>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}
export default Registrationpage
